  background:url('http://qiniupicbed.qiniudn.com/upload/62afc6156567c3ed79e5b6f2c64983af.png') no-repeat #252525
}
  .banner_bg{
  background-size:100% 99%;
  margin-top: -20px;
  text-align: center;
  /*padding:150px 0 290px;*/
  height:450px;
  }
  .banner_bg_t{
  color: #fff;
  padding:20px 40px;
  margin: 120px 0;
  display: inline-block;
  border-radius: 8px;
  border: 2px solid #fff;
  position: relative;
  -webkit-transition:1s!important;
  transition: 1s!important;
  opacity: 0;
  }
  .banner_bg_t:hover{
  background: #222;
  opacity: 0.8!important
}
  .ban_bor{
    position: relative;
    height: 6px;
    background: #252525;
    margin-top: -6px;
  }
  h2 {
    color: #525252
  }
  .hide_table{
    display: none;
  }
  .toggle{
    cursor: pointer;
  }
.toggle:hover{
  color: #fff;
  background: #999;
  -webkit-transition:.5s;
  transition: .5s;
}
